'use client'

import React, { createContext, useContext, useState } from 'react'

interface TooltipContextType {
  open: boolean
  onOpenChange: (open: boolean) => void
}

const TooltipContext = createContext<TooltipContextType | undefined>(undefined)

interface TooltipProps {
  children: React.ReactNode
}

const TooltipProvider = ({ children }: { children: React.ReactNode }) => {
  return <>{children}</>
}

const Tooltip = ({ children }: TooltipProps) => {
  const [open, setOpen] = useState(false)

  return (
    <TooltipContext.Provider value={{ open, onOpenChange: setOpen }}>
      <div className="relative inline-block">
        {children}
      </div>
    </TooltipContext.Provider>
  )
}

const TooltipTrigger = React.forwardRef<HTMLDivElement, React.HTMLAttributes<HTMLDivElement>>(
  ({ onMouseEnter, onMouseLeave, ...props }, ref) => {
    const context = useContext(TooltipContext)
    if (!context) throw new Error('TooltipTrigger must be used within Tooltip')
    
    return (
      <div
        ref={ref}
        onMouseEnter={(e) => {
          context.onOpenChange(true)
          onMouseEnter?.(e)
        }}
        onMouseLeave={(e) => {
          context.onOpenChange(false)
          onMouseLeave?.(e)
        }}
        {...props}
      />
    )
  }
)
TooltipTrigger.displayName = 'TooltipTrigger'

const TooltipContent = React.forwardRef<HTMLDivElement, React.HTMLAttributes<HTMLDivElement>>(
  ({ className = '', ...props }, ref) => {
    const context = useContext(TooltipContext)
    if (!context) throw new Error('TooltipContent must be used within Tooltip')
    
    if (!context.open) return null
    
    return (
      <div
        ref={ref}
        className={`absolute z-50 overflow-hidden rounded-md border bg-popover px-3 py-1.5 text-sm text-popover-foreground shadow-md animate-in fade-in-0 zoom-in-95 ${className}`}
        style={{ bottom: '100%', left: '50%', transform: 'translateX(-50%)', marginBottom: '4px' }}
        {...props}
      />
    )
  }
)
TooltipContent.displayName = 'TooltipContent'

export { Tooltip, TooltipTrigger, TooltipContent, TooltipProvider }